<template>
  <g-signin-button  :params="googleSignInParams" @success="onSignInSuccess" @error="onSignInError">

  </g-signin-button>
</template>

<script>
  export default {
    data() {
      return {

        googleSignInParams: {
          client_id: '849185429704-5l0okjgmk3dbteultbksh83bat7tn9b6.apps.googleusercontent.com'
        }
      }
    },
    methods: {
      onSignInSuccess(googleUser) {
         //const profile = googleUser.getBasicProfile()
        // console.log(googleUser)
        // console.log(googleUser.tc.idpId)
        // console.log(googleUser.Aa)
        // console.log(googleUser.tc.id_token)

      //  console.log(googleUser)
    
        this.$emit('isShowZZ')

        axios.post("https://customer.api.qbaytek.com/third_party_login/v1", {
          "third_party_name": `google`,
          "third_party_id": `${googleUser.Aa}`,
          "third_party_token":`${googleUser.qc.id_token}`

        }).then(res=>{
          //console.log(res.data)
         // this.$store.commit('userInfo',res.data)
		 localStorage.removeItem('userInfo')
		 localStorage.removeItem('remember')
		  localStorage.setItem('userInfo',JSON.stringify(res.data))
          console.log('谷歌登录后的信息')
          this.$router.push('/About')
        }).catch(err=>{
          console.log(err.response)
        })
      },
      onSignInError(error) {

        console.log('ERR', error)
      }
    }
  }
</script>

