<template>
    <VueLineLogin
    client-id="1655709810"
    callback-uri="http://localhost:8080/#/login"
    client-secret="5a8ae6ca87c8270c757ba8ec49c8a983"
    login-text=""
    @result="result"
   ></VueLineLogin>
</template>
<script>

import VueLineLogin from '@team-decorate/vue-line-login'

export default {
    components: {
        VueLineLogin
    },

    methods: {
        result(res) {
          axios.post("https://customer.api.qbaytek.com/third_party_login/v1", {
            "third_party_name": `line`,
            "third_party_id": `${res.userId}`,
            "third_party_token":`${res.id_token}`

          }).then(res=>{
            //console.log(res.data)

           // this.$store.commit('userInfo',res.data)
           localStorage.removeItem('userInfo')
           localStorage.removeItem('remember')
            localStorage.setItem('userInfo',JSON.stringify(res.data))
            console.log('line登录后的信息')
            this.$router.push('/About')
          }).catch(err=>{
            console.log(err.response)
          })
        },

    }
}
</script>
