<template>
  <div id="tel-regist">

    <img class="return" @click="return1" src="../assets/regist/fanhui_hei_icon.png" />
    <div class="registLabel">{{$t("regist.register_with_phone")}}</div>


      <span class="label-tel">{{$t('regist.set_password')}}</span>
      <input ref='password' class="tel" type="password" v-model="password"  :placeholder="$t('subAccount.nameLengthErrMsg')">
      <img src="../assets/regist/zc_yanjing2.svg" class="visible" v-show="password && !visible" @click="passwordVisible">
      <img  src="../assets/regist/zc_yanjing1.svg" class="visible" v-show="password && visible" @click="passwordVisible" />



      <span class="label-captcha">{{$t('subAccount.confirmPassword')}}</span>
      <input ref='passwordAgain' class="captcha" type="password" v-model="passwordAgain" :placeholder="$t('subAccount.confirmPasswordAgian')">
      <img src="../assets/regist/zc_yanjing2.svg" class="get-captcha" v-show="passwordAgain && !visibleAgain" @click="passwordAgainVisible">
      <img  src="../assets/regist/zc_yanjing1.svg" class="get-captcha" v-show="passwordAgain && visibleAgain" @click="passwordAgainVisible" />


    <img class="next" src="../assets/regist/zc_xiayibu2_botton.png"  v-show="!password||!passwordAgain" >
    <img class="next" src="../assets/regist/zc_xiayibu1_botton.png"  v-show="password&&passwordAgain" @click="next"/>



    <label class="menu_label2" @click="toLogin">
      <span> {{$t('login.loginTitle')}}</span>
    </label>



    <div id="ZZ" v-show="false"></div>
  </div>
</template>
<script>
  export default {

    data() {
      return {
        input1: '',
        showAlert: false,
        showAlert2: false,
        password: '',
        passwordAgain:'',

        visible:false,
        visibleAgain:false
      }
    },
    methods: {
      toLogin() {
        this.$router.push('/login')
      },
      return1() {
        this.$router.go(-1)
      },
      clearpassword(){
          this.password = ''
      },
      passwordVisible(){
        this.visible = !this.visible
        if(this.visible){
           this.$refs.password.type = 'text'
        }else{
          this.$refs.password.type = 'password'
        }

        // console.log(this.$refs.password)
      },
      passwordAgainVisible(){
        this.visibleAgain = !this.visibleAgain
        if(this.visibleAgain){
           this.$refs.passwordAgain.type = 'text'
        }else{
          this.$refs.passwordAgain.type = 'password'
        }
      },
      next() {

        if(this.password==this.passwordAgain){
          axios.post('https://jzk80vaqs7.execute-api.ap-northeast-1.amazonaws.com/dev/phone_signup/v1',{
             "phone":`${this.value}${this.telnumber}`,
              "password": `${this.password}`,
                "code": `${this.captcha}`
          }).then(res=>{
            console.log(res.data)
            alert(this.$t('regist.regist_successfully'))
            this.$router.push('/login')
          }).catch(err=>{
            alert(this.$t('error.unknownErr'))
            console.log(err.response)
          })
        }else{
          alert(this.$t('regist.inconsistent'))
        }
      },
    },
    watch: {
      showAlert2(newValue, oldValue) {

        if (!newValue && oldValue) {
          this.$router.push('/login')
        }
      },
      password(newValue,oldValue){
        if(newValue==''){
          this.$refs.password.type = 'password'
          this.visible = false
        }
      },
      passwordAgain(newValue,oldValue){
        if(newValue==''){
          this.$refs.passwordAgain.type = 'password'
          this.visibleAgain = false
        }
      }
    },
    computed:{
      telnumber:{
        get(){
          return this.$store.state.telNumber
        },
      },
      captcha:{
        get(){
          return this.$store.state.captcha
        },
      },
      value:{
        get(){
          return this.$store.state.areaCode
        },
      }
    },

  }
</script>
