<template>
  <div>

    <img class="return" @click="return1" src="../assets/regist/fanhui_hei_icon.png" />
      <img class="qbay-logo" src="../assets/qbay_logo.svg" >

    <div class="registLabel">{{$t('login.forget')}}</div>


    <span class="label">{{$t('login.emailPlaceholder')}}</span>

    <input class="email" type="email" v-model="email" :placeholder="$t('login.emailErrMsg')">

    <button @click="sendEmail" class="send-email">{{$t('login.send_registration_email')}}</button>







  </div>
</template>
<script>
  import { MessageBox } from 'mint-ui';
  export default {

    data() {
      return {
        input1: '',
        showAlert: false,
        showAlert2: false,
        email: '',
      }
    },
    methods: {
      toLogin() {
       this.$router.push('/login')
      },
      return1() {
        this.$router.go(-1)
      },


      sendEmail() {
        if(!this.email || !this.email.includes('@')){
            alert(this.$t('error.email_err'))  //请输入正确邮箱
            return
        }
       // alert('邮件已发送')
        axios.post('https://jzk80vaqs7.execute-api.ap-northeast-1.amazonaws.com/dev/send_reset_password_email/v1', {
          "email": `${this.email}`
        }).then(res => {
          console.log(res)
          if (res.status === 200) {
            // this.showAlert2 = true
            // console.log('邮件已发送')
            // MessageBox('提示', '操作成功');
            alert(this.$t('login.send_email_success'))
             this.$router.push('/login')
          }
        }).catch(err => {
          console.log(err.response)

          // this.showAlert = true
          if (err.response.data.code === 2003) { //错误返回信息
            alert(this.$t('error.email_err'))  //请输入正确邮箱
          }
          else if (err.response.data.code === 2004) { //错误返回信息
            alert(this.$t('error.emailErr'))   //用户不存在
          }
          else{
            alert(this.$t('email.email_sent_failed'))
          }

        })
      }
    },
    watch: {
      showAlert2(newValue, oldValue) {

        if (!newValue && oldValue) {
          this.$router.push('/login')
        }
      }
    }
  }
</script>
